// Import Popper
import * as Popper from '@popperjs/core';

// Import just the Bootstrap components we need
import Dropdown from '../node_modules/bootstrap/js/dist/dropdown';
import Modal from '../node_modules/bootstrap/js/dist/modal';
import Collapse from '../node_modules/bootstrap/js/dist/collapse';

// Initialize the components
const dropdownElements = document.querySelectorAll('[data-bs-toggle="dropdown"]');
dropdownElements.forEach(element => new Dropdown(element));

const modalElements = document.querySelectorAll('[data-bs-toggle="modal"]');
modalElements.forEach(element => new Modal(element));

const collapseElements = document.querySelectorAll('[data-bs-toggle="collapse"]');
collapseElements.forEach(element => new Collapse(element));

// Make Popper available globally if needed
window.Popper = Popper;


let lastUpdated = 0;
function checkForUpdates() {
    // Get the relative base path from a data attribute on the body tag
    let basePath = document.body.dataset.basePath || '';
    const sentinelPath = `${basePath}sentinel.json`;

    fetch(sentinelPath)
        .then(response => response.json())
        .then(data => {
            if (lastUpdated === 0) {
                lastUpdated = data.last_updated;
            } else if (data.last_updated > lastUpdated) {
                location.reload();
            }
        });
}
if (window.location.hostname === 'localhost') {
    setInterval(checkForUpdates, 100); // Check every second
}

